import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { Award } from "lucide-react"
import { resolveIpfsUrl } from "@/lib/utils"

const ResultsSection = ({ campaignInfo, proposals, candidates }) => {
  // Calculate total votes for percentage calculations
  const totalVotes = campaignInfo.totalVotes > 0 ? Number(campaignInfo.totalVotes) : 0

  return (
    <Card className="border-purple-800 bg-purple-900 shadow-lg">
      <CardHeader>
        <CardTitle className="flex items-center gap-2 text-purple-100">
          <Award className="h-5 w-5 text-purple-300" />
          Results
        </CardTitle>
        <CardDescription className="text-purple-300">
          {campaignInfo.status === 2
            ? "Final results of the campaign"
            : "Preliminary results (campaign not officially ended yet)"}
        </CardDescription>
      </CardHeader>

      <CardContent className="space-y-6">
        {/* Display Proposals Results */}
        {campaignInfo.votingType === 1 && proposals.length > 0 && (
          <div className="space-y-4">
            {proposals
              .sort((a, b) => Number(b.voteCount - a.voteCount)) // Sort by votes
              .map((proposal, index) => (
                <div key={proposal.id.toString()} className="space-y-2">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      {index === 0 && campaignInfo.resultType === 1 && (
                        <Badge className="bg-yellow-600 text-white">Winner</Badge>
                      )}
                      <span className="font-medium text-purple-100">{proposal.content}</span>
                    </div>
                    <div className="text-right">
                      <span className="font-bold text-purple-100">{proposal.voteCount.toString()}</span>
                      <span className="text-purple-300 text-sm ml-1">
                        ({totalVotes > 0 ? Math.round((Number(proposal.voteCount) / totalVotes) * 100) : 0}%)
                      </span>
                    </div>
                  </div>
                  <Progress
                    value={totalVotes > 0 ? (Number(proposal.voteCount) / totalVotes) * 100 : 0}
                    className="h-2 bg-purple-800"
                    indicatorClassName={
                      index === 0 && campaignInfo.resultType === 1 ? "bg-yellow-500" : "bg-purple-400"
                    }
                  />
                </div>
              ))}
          </div>
        )}

        {/* Display Candidates Results */}
        {campaignInfo.votingType === 0 && candidates.length > 0 && (
          <div className="space-y-4">
            {candidates
              .sort((a, b) => Number(b.voteCount - a.voteCount)) // Sort by votes
              .map((candidate, index) => (
                <div key={candidate.id.toString()} className="space-y-2">
                  <div className="flex items-start gap-3">
                    {candidate.imageHash && (
                      <img
                        src={resolveIpfsUrl(candidate.imageHash) || "/placeholder.svg"}
                        alt={candidate.name}
                        className="w-12 h-12 rounded-full object-cover flex-shrink-0 border-2 border-purple-600"
                        onError={(e) => {
                          e.target.style.display = "none"
                        }}
                      />
                    )}
                    <div className="flex-grow">
                      <div className="flex items-center gap-2">
                        {index === 0 && campaignInfo.resultType === 1 && (
                          <Badge className="bg-yellow-600 text-white">Winner</Badge>
                        )}
                        <span className="font-medium text-purple-100">{candidate.name}</span>
                      </div>
                      <p className="text-sm text-purple-300">{candidate.statement}</p>
                    </div>
                    <div className="text-right flex-shrink-0">
                      <span className="font-bold text-purple-100">{candidate.voteCount.toString()}</span>
                      <span className="text-purple-300 text-sm ml-1">
                        ({totalVotes > 0 ? Math.round((Number(candidate.voteCount) / totalVotes) * 100) : 0}%)
                      </span>
                    </div>
                  </div>
                  <Progress
                    value={totalVotes > 0 ? (Number(candidate.voteCount) / totalVotes) * 100 : 0}
                    className="h-2 bg-purple-800"
                    indicatorClassName={
                      index === 0 && campaignInfo.resultType === 1 ? "bg-yellow-500" : "bg-purple-400"
                    }
                  />
                </div>
              ))}
          </div>
        )}

        {proposals.length === 0 && candidates.length === 0 && (
          <div className="text-center text-purple-300 py-8">No results available</div>
        )}
      </CardContent>
    </Card>
  )
}

export default ResultsSection

