"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Vote, AlertCircle, CheckCircle } from "lucide-react"
import { toast } from "sonner"
import { useContract } from "@/context/contract-context"
import { resolveIpfsUrl, formatAddress } from "@/lib/utils"

const VotingSection = ({
  campaignInfo,
  proposals,
  candidates,
  isConnected,
  userIsRegistered,
  userHasVoted,
  needsRegistration,
  actionLoading,
  address,
  onVote,
  campaignId,
}) => {
  const { contract, signer } = useContract()
  const [voting, setVoting] = useState(false)
  const [votingForId, setVotingForId] = useState(null)

  const handleVote = async (targetId) => {
    if (!isConnected) {
      toast.error("Wallet not connected", {
        description: "Please connect your wallet to vote",
      })
      return
    }

    if (!contract || !signer || !campaignId || !address || campaignInfo === null) {
      toast.error("Cannot vote", {
        description: "Wallet not connected or contract not loaded",
      })
      return
    }

    // Ensure targetId is BigInt for contract call
    const targetIdBigInt = BigInt(targetId)

    setVoting(true)
    setVotingForId(targetId)
    try {
      const contractWithSigner = contract.connect(signer)
      let tx
      if (campaignInfo.votingType === 1 /* ProposalBased */) {
        tx = await contractWithSigner.voteForProposal(campaignId, targetIdBigInt)
      } else {
        // CandidateBased
        tx = await contractWithSigner.voteForCandidate(campaignId, targetIdBigInt)
      }

      toast.success("Vote submitted", {
        description: "Your vote has been sent to the blockchain",
      })

      await tx.wait()

      toast.success("Vote successful", {
        description: "Your vote has been recorded",
      })

      if (onVote) onVote()
    } catch (err) {
      console.error("Voting error:", err)
      let errorMessage = "Voting failed."
      if (err.reason) errorMessage = err.reason
      else if (err.data?.message) errorMessage = err.data.message
      else if (err.message) errorMessage = err.message

      toast.error("Voting failed", {
        description: errorMessage,
      })
    } finally {
      setVoting(false)
      setVotingForId(null)
    }
  }

  return (
    <Card className="border-purple-800 bg-purple-900 shadow-lg">
      <CardHeader>
        <CardTitle className="flex items-center gap-2 text-purple-100">
          <Vote className="h-5 w-5 text-purple-300" />
          Cast Your Vote
        </CardTitle>
        <CardDescription className="text-purple-300">
          {campaignInfo.votingType === 1 ? "Select a proposal to vote for" : "Select a candidate to vote for"}
        </CardDescription>
      </CardHeader>

      <CardContent className="space-y-4">
        {!isConnected ? (
          <div className="bg-purple-800/50 border border-purple-700 text-purple-200 rounded-md p-4 text-center">
            <AlertCircle className="h-5 w-5 mx-auto mb-2 text-purple-300" />
            <p>Please connect your wallet to vote</p>
          </div>
        ) : needsRegistration && !userIsRegistered ? (
          <div className="bg-yellow-900/30 border border-yellow-800 text-yellow-200 rounded-md p-4 text-center">
            <AlertCircle className="h-5 w-5 mx-auto mb-2 text-yellow-300" />
            <p>You must be registered to vote in this campaign</p>
          </div>
        ) : userHasVoted ? (
          <div className="bg-green-900/30 border border-green-800 text-green-200 rounded-md p-4 text-center">
            <CheckCircle className="h-5 w-5 mx-auto mb-2 text-green-300" />
            <p>You have already voted in this campaign</p>
          </div>
        ) : (
          <div className="space-y-4">
            {/* Render Proposals */}
            {campaignInfo.votingType === 1 && proposals.length > 0 && (
              <div className="space-y-3">
                {proposals.map((proposal) => (
                  <div
                    key={proposal.id.toString()}
                    className="border border-purple-700 rounded-lg p-4 hover:bg-purple-800/50 transition-colors"
                  >
                    <div className="flex justify-between items-center">
                      <div className="font-medium text-purple-100">{proposal.content}</div>
                      <Button
                        onClick={() => handleVote(proposal.id)}
                        disabled={voting}
                        size="sm"
                        className="bg-purple-600 hover:bg-purple-700 text-white"
                      >
                        {voting && votingForId === proposal.id ? (
                          <>
                            <span className="animate-spin mr-2">⟳</span> Voting...
                          </>
                        ) : (
                          "Vote"
                        )}
                      </Button>
                    </div>
                    <div className="mt-2 text-sm text-purple-300">Current votes: {proposal.voteCount.toString()}</div>
                  </div>
                ))}
              </div>
            )}

            {/* Render Candidates */}
            {campaignInfo.votingType === 0 && candidates.length > 0 && (
              <div className="space-y-4">
                {candidates.map((candidate) => (
                  <div
                    key={candidate.id.toString()}
                    className="border border-purple-700 rounded-lg p-4 hover:bg-purple-800/50 transition-colors"
                  >
                    <div className="flex items-start gap-4">
                      {candidate.imageHash && (
                        <div className="flex-shrink-0">
                          <img
                            src={resolveIpfsUrl(candidate.imageHash) || "/placeholder.svg"}
                            alt={candidate.name}
                            className="w-16 h-16 rounded-full object-cover border-2 border-purple-600"
                            onError={(e) => {
                              e.target.style.display = "none"
                            }}
                          />
                        </div>
                      )}
                      <div className="flex-grow">
                        <div className="font-medium text-purple-100">{candidate.name}</div>
                        <div className="text-sm text-purple-300 mt-1">{candidate.statement}</div>
                        <div className="text-xs text-purple-400 mt-1 font-mono">{formatAddress(candidate.address)}</div>
                      </div>
                      <div className="flex-shrink-0">
                        <Button
                          onClick={() => handleVote(candidate.id)}
                          disabled={voting || (needsRegistration && candidate.address === address)}
                          title={needsRegistration && candidate.address === address ? "Cannot vote for yourself" : ""}
                          size="sm"
                          className="bg-purple-600 hover:bg-purple-700 text-white"
                        >
                          {voting && votingForId === candidate.id ? (
                            <>
                              <span className="animate-spin mr-2">⟳</span> Voting...
                            </>
                          ) : (
                            "Vote"
                          )}
                        </Button>
                      </div>
                    </div>
                    <div className="mt-2 text-sm text-purple-300">Current votes: {candidate.voteCount.toString()}</div>
                  </div>
                ))}
              </div>
            )}

            {proposals.length === 0 && candidates.length === 0 && (
              <div className="text-center text-purple-300 py-8">
                No proposals or candidates available to vote on yet
              </div>
            )}
          </div>
        )}
      </CardContent>
    </Card>
  )
}

export default VotingSection

