"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Button } from "@/components/ui/button"
import { Award, Lock } from "lucide-react"
import { toast } from "sonner"
import { useContract } from "@/context/contract-context"

const CandidateRegistration = ({ campaignInfo, userIsRegistered, actionLoading, onRegister, campaignId }) => {
  const { contract, signer, isConnected, address } = useContract()
  const [candidateName, setCandidateName] = useState("")
  const [candidateStatement, setCandidateStatement] = useState("")
  const [candidateImageHash, setCandidateImageHash] = useState("")
  const [passKey, setPassKey] = useState("")
  const [registering, setRegistering] = useState(false)

  const handleRegisterAsCandidate = async () => {
    if (!isConnected) {
      toast.error("Wallet not connected", {
        description: "Please connect your wallet to register as a candidate",
      })
      return
    }

    if (!contract || !signer || !campaignId || !address) {
      toast.error("Cannot register", {
        description: "Wallet not connected or contract not loaded",
      })
      return
    }

    if (!candidateName || !candidateStatement) {
      toast.error("Missing required fields", {
        description: "Candidate name and statement are required",
      })
      return
    }

    if (campaignInfo?.restriction === 2 /* RequiredRegistration */ && !passKey && !userIsRegistered) {
      toast.error("Missing passkey", {
        description: "Passkey is required for this campaign's registration",
      })
      return
    }

    setRegistering(true)
    try {
      const contractWithSigner = contract.connect(signer)
      const tx = await contractWithSigner.registerAsCandidate(
        campaignId,
        candidateName,
        candidateStatement,
        candidateImageHash,
        passKey,
      )

      toast.success("Candidate registration submitted", {
        description: "Your registration transaction has been sent to the blockchain",
      })

      await tx.wait()

      toast.success("Candidate registration successful", {
        description: "You are now registered as a candidate in this campaign",
      })

      if (onRegister) onRegister()
      // Clear form fields
      setCandidateName("")
      setCandidateStatement("")
      setCandidateImageHash("")
      setPassKey("")
    } catch (err) {
      console.error("Candidate registration error:", err)
      let errorMessage = "Candidate registration failed."
      if (err.reason) errorMessage = err.reason
      else if (err.data?.message) errorMessage = err.data.message
      else if (err.message) errorMessage = err.message

      toast.error("Candidate registration failed", {
        description: errorMessage,
      })
    } finally {
      setRegistering(false)
    }
  }

  return (
    <Card className="border-purple-800 bg-purple-900 shadow-lg">
      <CardHeader>
        <CardTitle className="flex items-center gap-2 text-purple-100">
          <Award className="h-5 w-5 text-purple-300" />
          Register as a Candidate
        </CardTitle>
        <CardDescription className="text-purple-300">
          Fill out the form to become a candidate in this campaign
        </CardDescription>
      </CardHeader>

      <CardContent className="space-y-4">
        <div className="space-y-2">
          <label htmlFor="candidateName" className="text-sm font-medium text-purple-200">
            Candidate Name <span className="text-red-400">*</span>
          </label>
          <Input
            id="candidateName"
            value={candidateName}
            onChange={(e) => setCandidateName(e.target.value)}
            placeholder="Your name or title"
            required
            className="bg-purple-950 border-purple-700 text-purple-100 placeholder:text-purple-500"
          />
        </div>

        <div className="space-y-2">
          <label htmlFor="candidateStatement" className="text-sm font-medium text-purple-200">
            Statement <span className="text-red-400">*</span>
          </label>
          <Textarea
            id="candidateStatement"
            value={candidateStatement}
            onChange={(e) => setCandidateStatement(e.target.value)}
            placeholder="Why should people vote for you?"
            rows="3"
            required
            className="bg-purple-950 border-purple-700 text-purple-100 placeholder:text-purple-500 min-h-[100px]"
          />
        </div>

        <div className="space-y-2">
          <label htmlFor="candidateImageHash" className="text-sm font-medium text-purple-200">
            Image IPFS Hash (Optional)
          </label>
          <Input
            id="candidateImageHash"
            value={candidateImageHash}
            onChange={(e) => setCandidateImageHash(e.target.value)}
            placeholder="Qm..."
            className="bg-purple-950 border-purple-700 text-purple-100 placeholder:text-purple-500"
          />
          <p className="text-xs text-purple-400">Upload your image to IPFS and paste the hash here</p>
        </div>

        {campaignInfo.restriction === 2 && !userIsRegistered && (
          <div className="space-y-2">
            <label htmlFor="candidatePassKey" className="text-sm font-medium text-purple-200">
              Passkey <span className="text-red-400">*</span>
            </label>
            <div className="flex items-center">
              <Lock className="h-4 w-4 mr-2 text-purple-400" />
              <Input
                type="password"
                id="candidatePassKey"
                value={passKey}
                onChange={(e) => setPassKey(e.target.value)}
                placeholder="Enter registration passkey"
                className="bg-purple-950 border-purple-700 text-purple-100 placeholder:text-purple-500"
              />
            </div>
          </div>
        )}

        <Button
          onClick={handleRegisterAsCandidate}
          disabled={registering}
          className="w-full bg-purple-600 hover:bg-purple-700 text-white"
        >
          {registering ? (
            <>
              <span className="animate-spin mr-2">⟳</span> Registering...
            </>
          ) : (
            "Register as Candidate"
          )}
        </Button>
      </CardContent>
    </Card>
  )
}

export default CandidateRegistration

